const initialState = {
  SetListMonevsReducer: [],
  formFilterMonev: {
    Year: 0,
  },
  modalDetailMonevReducer: false,
  GetDetailMonevReducer : []
};

const reportMonevReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_LIST_MONEVS":
      return { ...state, SetListMonevsReducer: action.payload };
      case "GET_LIST_MONEV_BY_IDMONEV" :
      return { ...state, GetDetailMonevReducer :action.payload};
    case "MODAL_REPORT_DETAIL":
      return { ...state, modalDetailMonevReducer: action.payload };
    case "FILTER_PERTANYAAN":
      return {
        ...state,
        formFilterMonev: {
          ...state.formFilterMonev,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};

export default reportMonevReducer;
