const initialState = {
  formKecamatan: {
    IdKecamata:0,
    IdKabupaten: 0,
    Kode: "",
    Nama: "",
    Alamat: "",
    Longitude: "",
    Latitude: "",
  },
  modalKec: false,
  DataKecamatanFilter: [],
};


const kecamatanReducer = (state = initialState, action) => {
  switch (action.type) {
    case "ADD_KECAMATAN":
      return {...state, formKecamatan: {
        ...state.formKecamatan,
        [action.formType] :action.formValue
      }}
    case "GET_KECAMATANS" :
      return {...state, DataKecamatanFilter:action.payload}
    case "MODAL_KEC" :
      return { ...state, modalKec:action.payload };
    case "RESET_FORM_KEC":
      return { ...state, formKecamatan: initialState.formKecamatan };
    default:
      return state
  }
}

export default kecamatanReducer;
