const initialState = {
  dataComboProv: [],
  dataComboKabupaten: [],
  dataComboKecamatan: [],
  dataComboJenisVariabel: [],
  dataComboJenisLokpri: [],
  dataComboJenisLokpriReducer: [],
  dataComboRoles: [],
  dataCmbPgwKecamatansByKecamatanReducer: [],
  dataCmbPgwKecamatansByJabatanReducer: [],
  dataCmbJabatanReducer: [],
  dataCmbJabatanReducer1: [],
};

const comboReducer = (state = initialState, action) => {
  switch (action.type) {
    case "DATA_CMB_PROV":
      return { ...state, dataComboProv: action.payload };
    case "DATA_CMB_KAB":
      return { ...state, dataComboKabupaten: action.payload };
    case "DATA_CMB_KAB_REST":
      return { ...state, dataComboKabupaten: initialState };
    case "DATA_CMB_KEC":
      return { ...state, dataComboKecamatan: action.payload };
      case "DATA_CMB_JABATAN" :
        return { ...state, dataCmbJabatanReducer:action.payload };
      case "DATA_CMB_JABATAN1" :
        return { ...state, dataCmbJabatanReducer1: action.payload };
    case "DATA_CMB_KECAMATAN_RESET":
      return { ...state, dataComboKecamatan: initialState };
    case "DATA_CMB_JENIS_VARIABEL":
      return { ...state, dataComboJenisVariabel: action.payload };
    case "DATA_CMB_LOKPRI":
      return { ...state, dataComboJenisLokpri: action.payload };
    case "DATA_CMB_LOKPRI_PARSE":
      return { ...state, dataComboJenisLokpriReducer: action.payload };
    case "DATA_CMB_PEGAWAI_KEC_BY_KEC":
      return {
        ...state,
        dataCmbPgwKecamatansByKecamatanReducer: action.payload,
      };
    case "DATA_CMB_PEGAWAI_KEC_BY_JABATAN":
      return {
        ...state,
        dataCmbPgwKecamatansByJabatanReducer: action.payload,
      };
    case "DATA_CMB_ROLES":
      return { ...state, dataComboRoles: action.payload };
    default:
      return state;
  }
};

export default comboReducer;
