const initialState = {
  BobotKondisiTdkLayakReducer: [],
  BobotKondisiCukupLayakReducer: [],
  BobotKondisiLayakReducer: [],
  ResultAddJawabansReducer: [],
  ResultGetVariabelIndikatorReduce: [],
};

const formStepReduce = (state = initialState, action) => {
  switch (action.type) {
    case "BOBOT_KONDISI_TIDAK_LAYAK":
      return { ...state, BobotKondisiTdkLayakReducer: action.payload };
    case "BOBOT_KONDISI_CUKUP_LAYAK":
      return { ...state, BobotKondisiCukupLayakReducer: action.payload };
    case "BOBOT_KONDISI_LAYAK":
      return { ...state, BobotKondisiLayakReducer: action.payload };
    case "RESULT_ADD_JAWABANS":
      return { ...state, ResultAddJawabansReducer: action.payload };
    case "RESULT_GET_VARIABEL_INDIKATOR":
      return [{ ...state, ResultGetVariabelIndikatorReduce: action.payload }];
    default:
      return state;
  }
};

export default formStepReduce;
