const initialState = {
  NamaVariabelReducer: "",
  GetListIndikatorReducer: [],
  modalIndikator:false,
  formIndikatorReducer: {
    IdIndikator: 0,
    IdVariabel: 0,
    Nama: "",
    Type: 0,
  },
};

const indikatorReducer = (state = initialState, action) => {
  switch (action.type) {
    case "NAMA_VARIABEL":
      return { ...state, NamaVariabelReducer: action.payload };
    case "GET_INDIKATORS":
      return { ...state, GetListIndikatorReducer: action.payload };
    case "MODAL_INDIKATOR":
      return { ...state, modalIndikator: action.payload };
    case "RESET_FORM_INDIKATOR":
      return {
        ...state,
        formIndikatorReducer: initialState.formIndikatorReducer,
      };
    case "ADD_INDIKATOR":
      return {
        ...state,
        formIndikatorReducer: {
          ...state.formIndikatorReducer,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};

export default indikatorReducer;
