const initialState = {
  GetDataUsers: [],
  modalUser: false,
  formUserReduce: {
    IdUser: 0,
    Username: "",
    Password: "",
    Email: "",
    FirstName: "",
    MiddleName: "",
    LastName: "",
    Address: "",
    PhoneNumber: "",
    MobileNumber: "",
    FileImage: "",
    Roles: [],
  },
};

const userReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_USER_LIST":
      return { ...state, GetDataUsers: action.payload };
    case "MODAL_USER":
      return { ...state, modalUser: action.payload };
    case "RESET_FORM_USER":
      return { ...state, formUserReduce: initialState.formUserReduce };
    case "ADD_USER":
      return {
        ...state,
        formUserReduce: {
          ...state.formUserReduce,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};
export default userReducer;
