const initialState = {
  SetApplSetingsReducer : [],
  SetFormSettingReducer : {
    code:"",
    name:"",
    valueString:""
  }
}

const settingsReducer  = (state = initialState, action) => {

  switch (action.type) {
    case "GET_LIST_APPL_SETTINGS":
      return { ...state, SetApplSetingsReducer:action.payload};
    case "RESET_FORM_SETTING":
      return {
        ...state,
        SetFormSettingReducer: initialState.SetFormSettingReducer,
      };
      case "ADD_APPL_SETTINGS":
        return {...state,
          SetFormSettingReducer:{
            ...state.SetFormSettingReducer,
            [action.formType] :action.formValue
          }
        }
    default:
      return state;
  }
}

export default settingsReducer;
