const initialState = {
  GetDataUsers: [],
  modalUserKec: false,
  formUserKecReduce: {
    IdUser: 0,
    Username: "",
    Password: "",
    Email: "",
    FirstName: "",
    MiddleName: "",
    LastName: "",
    Address: "",
    PhoneNumber: "",
    MobileNumber: "",
    FileImage: "",
    Roles:[],
    IdKecamatan:0,
    IdJabatan:0,
  },
};

const pegawaiKecamatanReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_USER_LIST":
      return { ...state, GetDataUsers: action.payload };
    case "MODAL_USER_KEC":
      return { ...state, modalUserKec: action.payload };
    case "RESET_FORM_USER":
      return { ...state, formUserKecReduce: initialState.formUserKecReduce };
    case "ADD_USER_KEC":
      return {
        ...state,
        formUserKecReduce: {
          ...state.formUserKecReduce,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};
export default pegawaiKecamatanReducer;
