const initialState = {
  GetListPertanyaanReducer: [],
  modalPertanyaan: false,
  formPertanyaanReducer: {
    IdPertanyaan: 0,
    Pertanyaan: "",
    BobotDefault: 0,
  },
};

const masterPertanyaanReducer = (state = initialState, action) => {
  switch (action.type) {
    case "GET_LIST_PERTANYAAN":
      return { ...state, GetListPertanyaanReducer: action.payload };
    case "MODAL_PERTANYAAN":
      return { ...state, modalPertanyaan: action.payload };
    case "RESET_FORM_PERTANYAAN":
      return {
        ...state,
        formPertanyaanReducer: initialState.formPertanyaanReducer,
      };
    case "ADD_PERTANYAAN":
      return {
        ...state,
        formPertanyaanReducer: {
          ...state.formPertanyaanReducer,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};

export default masterPertanyaanReducer;
