const initialState = {
  setfisrtFormAddMonevReduce: {
    IdPegawai: 0,
    IdJenisLokpri: 0,
  },
  setDropdownNama : {
    FullName: "",
    MobileNumber: "",
    Email: "",
    OfficeAddress: "",
  },
  checkFirtsForm : false,
  monevId:0,
  GetDataAddMonevTemporary : []
};

const addMonevReducer = (state = initialState, action) => {
    switch (action.type) {
      case "SET_CHANGE_NAME":
        return {
          ...state,
          setDropdownNama: {
            ...state.setDropdownNama,
            [action.formType]: action.formValue,
          },
        };
      case "SET_FIRST_ADD_FORM_MONEV":
        return {
          ...state,
          setfisrtFormAddMonevReduce: {
            ...state.setfisrtFormAddMonevReduce,
            [action.formType]: action.formValue,
          },
        };
        case "RESET_FIRST_ADD_FORM_MONEV":
          return {
            ...state,
            setfisrtFormAddMonevReduce: initialState.setfisrtFormAddMonevReduce,
          };
      case "FORM_INPUT_FIRST":
        return { ...state, checkFirtsForm: action.payload };
      case "GET_MONEV_ID":
        return { ...state, monevId: action.payload };
        case "GET_RESULT_ADD_TEMPORARY":
          return {...state, GetDataAddMonevTemporary:action.payload}
      default:
        return state;
    }
}



export default addMonevReducer;
