const initialState = {
  formLogin: {
    UsernameOrEmail: "",
    Password: "",
  },
  modalForget: false,
  formForgot: {
    UsernameOrEmailForgot: "",
  },
};

const loginReducer = (state = initialState, action) => {
  switch (action.type) {
    case "login":
      return {
        ...state,
        formLogin: {
          ...state.formLogin,
          [action.formType]: action.formValue,
        },
      };
    case "resetLogin":
      return { ...state, formLogin: initialState.formLogin };
    case "MODAL_FORGOT_PASSWORD":
      return { ...state, modalForget: action.payload };
    case "RESET_FORM_FORGOT":
      return { ...state, formForgot: initialState.formForgot };
    case "forgot":
      return {
        ...state,
        formForgot: {
          ...state.formForgot,
          [action.formType]: action.formValue,
        },
      };
    default:
      return state;
  }
};

export default loginReducer;
